body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.body-info {
  display: flex;
}

@media all and (max-width: 768px) {
  .body-info {
    display: block;
  }
}
.header {
  width: 100%; }
  .header .cover {
    background: url(/static/media/cover.431b4175.jpg) no-repeat center center;
    background-size: cover;
    position: relative;
    height: 20vw; }
    @media all and (max-width: 768px) {
      .header .cover {
        height: 50vw; } }
    .header .cover .cover-overlay {
      width: 100%;
      height: 100%;
      background: -webkit-gradient(linear, left bottom, left top, from(black), to(rgba(68, 33, 30, 0.2)));
      background: -webkit-linear-gradient(bottom, black, rgba(68, 33, 30, 0.2));
      background: linear-gradient(to top, black, rgba(68, 33, 30, 0.2));
      border-bottom: #000000 solid 1px; }
    .header .cover .avatar {
      position: absolute;
      bottom: calc(8vw - 200px);
      left: 15vw;
      z-index: 1; }
      @media all and (max-width: 768px) {
        .header .cover .avatar {
          bottom: -100px;
          left: calc(50vw - 100px); } }
    .header .cover .name {
      color: white;
      position: absolute;
      font-size: 60px;
      font-family: 'Dancing Script', cursive;
      line-height: 1;
      bottom: calc(4vw - 30px);
      left: calc(25vw + 200px);
      z-index: 1; }
      @media all and (max-width: 768px) {
        .header .cover .name {
          font-size: 40px;
          width: 100%;
          bottom: calc(-5vw - 150px);
          left: 0;
          text-align: center; } }
    .header .cover .career {
      color: black;
      position: absolute;
      font-size: 30px;
      line-height: 1;
      bottom: calc(4vw - 115px);
      left: calc(25vw + 230px);
      z-index: 1; }
      @media all and (max-width: 768px) {
        .header .cover .career {
          font-size: 20px;
          color: white;
          width: 100%;
          bottom: calc(-5vw - 180px);
          left: 0;
          text-align: center; } }

.basic-info {
  background: #000000;
  margin-left: 5vw;
  width: calc(20vw + 200px);
  height: auto;
  height: initial;
  padding: calc(224px - 8vw) 0 20px; }
  @media all and (max-width: 768px) {
    .basic-info {
      width: 100%;
      margin-left: 0;
      padding-top: calc(5vw + 200px);
      height: 100%; } }
  .basic-info .media-social {
    margin: 0 calc(10vw + 16px);
    display: flex; }
    @media all and (max-width: 768px) {
      .basic-info .media-social {
        margin: 0 calc(50vw - 88px); } }
    .basic-info .media-social i {
      color: white;
      font-size: 24px;
      margin: auto 8px; }
      .basic-info .media-social i:hover {
        color: #909090; }
  .basic-info .contact {
    color: white;
    margin: 10px;
    text-align: center; }
    @media all and (max-width: 768px) {
      .basic-info .contact {
        margin: 10px calc(50vw - 100px); } }
    .basic-info .contact i {
      margin: 8px; }
    .basic-info .contact a {
      color: white; }
      .basic-info .contact a:hover {
        color: #909090; }
  .basic-info .info span {
    color: white; }
  .basic-info .info .info-item {
    color: white;
    margin: 0 10vw;
    border-bottom-style: dashed; }
    @media all and (max-width: 768px) {
      .basic-info .info .info-item {
        margin: 0 calc(50vw - 100px); } }
    .basic-info .info .info-item:last-child {
      border-bottom-style: none; }
    .basic-info .info .info-item .info-italic {
      font-style: italic;
      text-align: right;
      width: 50%;
      float: right; }
  .basic-info .info .info-text {
    color: white;
    margin: 0 20px;
    text-align: center; }

.background {
  margin: calc(200px - 8vw) 30px 30px;
  width: calc(75vw - 260px); }
  @media all and (max-width: 768px) {
    .background {
      margin: 0;
      width: 100%; } }
  .background .card-list .ant-list-item-meta-content {
    flex: auto; }
    .background .card-list .ant-list-item-meta-content h4 {
      white-space: pre-wrap;
      color: #000000;
      font-weight: bold; }
  .background .card-list .ant-list-item-content {
    display: block; }
    .background .card-list .ant-list-item-content i {
      color: #000000;
      text-align: left;
      width: 100%; }

